@import '/node_modules/normalize.css/normalize.css';
@import './sass/fonts.scss';
@import './sass/general.scss';

* {
	font-family: 'Roboto';
	box-sizing: border-box;
}

ul,
ol {
	list-style: none;
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
}

html,
body {
	overflow-x: hidden;
}

html.no-scroll {
	overflow: hidden;
}

p {
	margin: 0;
}

a {
	text-decoration: none;
	color: #000;
}
